export default {
	data() {
		return {
			page_title: '珠海大横琴科技发展有限公司',
			login_show:true,
			pers_card_msg:{
			}
		}
	},
	onLoad(options){
		if(options.card){
			this.pers_card_msg=JSON.parse(unescape(options.card))
		}else if(options.id){
			let _this=this
			for(var i in _this.card_list){
				if(_this.card_list[i].id+""===options.id){
					_this.pers_card_msg=_this.card_list[i]
					break
				}
			}
		}else{
			
		}
	},
	onShow(){
		// if(requirePlugin('chooseLocation').getLocation()){
		// 	let location =requirePlugin('chooseLocation').getLocation();
		// 	uni.showModal({
		// 		content:JSON.stringify(location)
		// 	})
		// }
	},
	onShareAppMessage(res) {
	    if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
		let tname=this.pers_card_msg.name,id=this.pers_card_msg.id
	    return {
	      title: tname,
	      path: '/pages/card_desc/index?id='+id
	    }
	},
	computed: {
		
	},
	methods: {
		getLogoUrl(){
			return this.pers_card_msg.logo
		},
		callCardPNo(no){
			if(no){
				uni.makePhoneCall({
				    phoneNumber: no
				});
			}
		},
		addPhone(){
			if(this.pers_card_msg.pho_no){
				let nickName=this.pers_card_msg.name,
				mobilePhoneNumber=this.pers_card_msg.pho_no,
				lastName=nickName.substring(1,nickName.length),
				firstName=nickName.substring(0,1)
				uni.addPhoneContact({
				    nickName: nickName,
				    lastName: lastName,
				    firstName: firstName,
				    remark: '',
				    mobilePhoneNumber: mobilePhoneNumber,
				    weChatNumber: '',
				    success: function () {
				        console.log('success');
				    },
				    fail: function () {
				        console.log('fail');
				    }
				});
			}
		},
		toLocation(){  //2. 获取地理位置
			var _this=this;
			try{
				let laddr,wkey,referer;
				laddr=_this.pers_card_msg.addr;
				referer='快享电名'
				wkey='UVTBZ-Y7JCK-XYGJU-AYUFD-QXRAF-LRF3N'
				uni.getLocation({
					type: 'wgs84',
					success (res) {
						let latitude,longitude;
						latitude = res.latitude.toString();
						longitude = res.longitude.toString();
						//const key = ''; //使用在腾讯位置服务申请的key
						//const referer = ''; //调用插件的app的名称
						const location = JSON.stringify({
						  latitude: latitude,
						  longitude: longitude
						});
						//const category = '工作服务';
						//let plugin = requirePlugin('chooseLocation')
						wx.navigateTo({
						  url: `plugin://chooseLocation/index?key=${wkey}&referer=${referer}&location=${location}`
						});
					}
				});
			}catch(e){
				uni.showModal({
					content:e.toString()
				})
			}
		},
		isGetLocation(){ // 3. 检查当前是否已经授权访问scope属性，参考下截图
			var _this=this;
			uni.getSetting({
				success(res) {
					if (!res.authSetting["scope.userLocation"]) {  //3.1 每次进入程序判断当前是否获得授权，如果没有就去获得授权，如果获得授权，就直接获取当前地理位置
						uni.authorize({
							scope: "scope.userLocation",
							success() { //1.1 允许授权
								_this.toLocation();
							},
							fail(){    //1.2 拒绝授权
								console.log("你拒绝了授权，无法获得周边信息")
							}
						})
					}else{
						_this.toLocation()
					}
				}
			});
		},
		saveCardList(){
			
		},
		saveEwm(e){
			//获取相册授权
			let _this=this
			uni.getSetting({
				success(res) {
				    if (!res.authSetting['scope.writePhotosAlbum']) {
						uni.authorize({
							scope: 'scope.writePhotosAlbum',
							success() {
								//这里是用户同意授权后的回调
								_this.shareToWeixin("WXSceneSession");
							},
							fail() {//这里是用户拒绝授权后的回调
							
							}
						 })
				   } else {//用户已经授权过了
					    _this.shareToWeixin("WXSceneSession");
				   }
				}
			})
		},
		saveImgToLocal(e){
			uni.showModal({
				title: '提示',
				content: '确定保存到相册吗',
				success: function (res) {
					if (res.confirm) {
						uni.downloadFile({
							url:"",//图片地址
							success: (res) =>{
								if (res.statusCode === 200){
									uni.saveImageToPhotosAlbum({
										filePath: res.tempFilePath,
										success: function() {
											uni.showToast({
												title: "保存成功",
												icon: "none"
											});
										},
										fail: function() {
											uni.showToast({
												title: "保存失败",
												icon: "none"
											});
										}
									});
								} 
							}
						})
					} else if (res.cancel) {
						
					}
				}
			});
		},
		shareToWeixin(scene) {
			uni.chooseImage({
			  count: 1,
			  sizeType: ['compressed'],
			  sourceType: ['album'],
			  success: (res) => {
				uni.showToast({
					title: '选择成功！'
				})
				uni.share({
				  provider: 'weixin',
				  type: 2,
				  title: 'app微信分享测试',
				  scene: scene,
				  imageUrl: res.tempFilePaths[0],
				  success: (res) => {
					// console.log(res)
					uni.showToast({
					  title: '分享成功！'
					})
				  },
				  fail: (err) => {
					// console.log(err)
				  }
				})
			  }
			})   
		}
	}
}
